<template>
  <div class="lottery-Bets-Item state-0" @click="open">
    <div class="top">
      <p><img :src="item.icon" alt=""><span>{{ item.season_match_name }}</span></p>
      <p><span>{{ item.game_start_time|dateFormat('hh:mm') }}</span><span>BO1</span></p>
      <p class="active"><span></span></p>
    </div>
    <div class="center">
      <div v-if="odds('v3')"><span>主胜</span><span>{{ odds('v3').v3 }}</span></div>
      <div v-if="odds('v1')"><span>平</span><span>{{ odds('v1').v1 }}</span></div>
      <div v-if="odds('v0')"><span>主负</span><span>{{ odds('v0').v0 }}</span></div>
    </div>
    <div class="bottom">
      <div><img :src="item.host_team_logo" alt=""></div>
      <div><span>{{ item.host_team_name }}</span><span>VS</span><span>{{ item.guest_team_name }}</span></div>
      <div><img :src="item.guest_team_logo" alt=""></div>
    </div>
  </div>
</template>

<script>//
import { token } from '../../tool/storeManage'
import { Toast } from 'mint-ui'

export default {
  name: 'LotteryBetsItem',
  props: {
    item: { type: Object }
  },
  methods: {
    odds (type) {
      if (this.item.odd) {
        const item = this.item.odd.find(i => {
          return i[type] !== undefined
        })
        if (item === -1) return null
        return item
      }
      return null
    },
    open () {
      if (!token.Token) {
        Toast('请登陆')
        this.$router.replace({
          path: '/login'
        })
        return
      }
      this.$router.push({
        path: '/webPage',
        query: { url: this.item.link, title: '365彩票', showHead: 'false', post: true }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  $border: 4px;
  .item {
    height: 36px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
  }

  .item2 {
    border-top: 1px solid #494949;
  }

  .lottery-Bets-Item {
    margin-top: 5px;
    > div.top {
      height: 26px;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      background-color: #1C1C1C;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      > p {
        width: 33.3%;
      }
      > p:nth-child(1) {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 1;
        font-size: 12px;
        color: #999;
        span {
          vertical-align: middle;
        }
        img {
          line-height: 1;
          margin: 0 5px;
          width: 20px;
          height: 20px;
          vertical-align: middle;
        }
      }
      > p:nth-child(2) {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
        span {
          color: #999;
        }
      }
      > p:nth-child(3) {
        color: #FFC63A;
        position: relative;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
      }
      > p:nth-child(3).active::after {
        content: '';
        display: inline-block;
        position: absolute;
        right: 0;
        width: 35px;
        height: 35px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("../../assets/competition/ic_guss@2x.png");
      }
    }
    > div.center {
      display: flex;
      flex-flow: row nowrap;
      height: 26px;
      background: linear-gradient(180deg, rgba(63, 63, 63, 1) 0%, rgba(49, 49, 49, 1) 100%);
      > div {
        width: 33.3%;
        flex: 1 1;
        text-align: center;
        line-height: 26px;
        border-right: 1px solid #494949;
        span:first-child {
          color: #999999;
          margin-right: 10px;
        }
        span:last-child {
          color: #FFC63A;
        }
      }
      > div:last-child {
        border-right: none;
      }
    }
    > div.bottom {
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      border-top: 1px solid #494949;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      height: 36px;
      background-color: #313131;
      > div:nth-child(2) {
        height: 100%;
        width: 50%;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        > span:nth-child(2) {
          width: 15%;
          text-align: center;
        }
        > span:nth-child(1), > span:nth-child(3) {
          width: 42.5%;
          max-height: 100%;
          overflow: hidden;
          text-align: center;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
      > div:nth-child(1), > div:nth-child(3) {
        height: 100%;
        width: 25%;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        img {
          width: 26px;
        }
      }
    }
  }

  .state-1 {
    > div.top {
      > p:nth-child(3) {
        color: #999;
      }
    }
  }
</style>
